import { defineStore } from "pinia";
import BigNumber from "bignumber.js";
import settings from "@/store/settings";
import busd_abi from "@/assets/abis/busd_abi.json";
import presale_abi from "@/assets/abis/presale_abi.json";

interface PresaleStore {
  addresses: {
    busdContract: string;
    presaleContract: string;
  };
  dates: {
    start: Date;
    end: Date;
  };
  token: {
    walletAmount: number;
    contribution: number;
    maxContribution: number;
    approveAmount: string;
    claimed: number;
    price: number;
    totalInvested: number;
    hardcap: number;
  };
  isConnected: boolean;
  isWhitelisted: boolean;
  isApproved: boolean;
  started: boolean;
  wallet: string;
  busdContract: any;
  presaleContract: any;
}

export const useStore = defineStore({
  id: "presale",
  state: () =>
    ({
      addresses: {
        busdContract: settings.busdContract,
        presaleContract: settings.presaleContract,
      },
      dates: {
        start: settings.dates.start,
        end: settings.dates.end,
      },
      token: {
        walletAmount: 0,
        contribution: 0,
        maxContribution: 0,
        approveAmount: new BigNumber(settings.approveAmount)
          .times(10 ** 18)
          .toString(10),
        claimed: 0,
        price: 0,
        totalInvested: 1,
        hardcap: 0,
      },
      isConnected: false,
      isWhitelisted: false,
      isApproved: false,
      started: false,
      wallet: "",
      busdContract: {},
      presaleContract: {},
    } as PresaleStore),

  actions: {
    loadContractData(): void {
      this.checkBusdBalance();
      this.checkTokenPrice();
      this.checkContribution();
      this.checkClaimed();
      this.checkWhitelist();
      this.checkAllowance();
      this.checkStarted();
      this.checkTotalInvested();
      this.checkHardcap();
    },
    loadContracts(): void {
      this.busdContract = new window.web3.eth.Contract(
        busd_abi,
        this.addresses.busdContract
      );
      this.presaleContract = new window.web3.eth.Contract(
        presale_abi,
        this.addresses.presaleContract
      );
    },
    contribute(contribution: number) {
      this.presaleContract.methods
        .deposit(new BigNumber(contribution).times(10 ** 18).toString())
        .send({ from: this.wallet })
        .then(() => {
          this.checkContribution();
          this.checkBusdBalance();
        });
    },
    approveContract(): void {
      this.busdContract.methods
        .approve(this.addresses.presaleContract, this.token.approveAmount)
        .send({ from: this.wallet })
        .then((res: boolean) => {
          if (res) {
            this.isApproved = res;
          }
        });
    },
    checkBusdBalance() {
      this.busdContract.methods
        .balanceOf(this.wallet)
        .call()
        .then((res: number) => {
          this.token.walletAmount = res;
        });
    },
    checkTotalInvested() {
      this.presaleContract.methods
        .totalInvested_BUSD()
        .call()
        .then((res: number) => {
          this.token.totalInvested = res;
        });
    },
    checkAllowance() {
      this.busdContract.methods
        .allowance(this.wallet, this.addresses.presaleContract)
        .call()
        .then((res: number) => {
          if (res > 0) {
            this.isApproved = true;
          }
        });
    },
    checkWhitelist() {
      this.presaleContract.methods
        .whitelist(this.wallet)
        .call()
        .then((res: number) => {
          switch (true) {
            case res == 1:
              this.isWhitelisted = true;
              this.checkMaxContribution();
              break;
            case res > 1:
              this.isWhitelisted = true;
              this.token.maxContribution = res;
              break;
            default:
              // not whitelisted
              break;
          }
        });
    },
    checkMaxContribution() {
      this.presaleContract.methods
        .maxUserDeposits()
        .call()
        .then((res: number) => {
          this.token.maxContribution = res;
        });
    },
    checkContribution() {
      this.presaleContract.methods
        .getMyDeposits(this.wallet)
        .call()
        .then((res: number) => {
          this.token.contribution = res;
        });
    },
    checkTokenPrice() {
      this.presaleContract.methods
        .pistonTokenPrice()
        .call()
        .then((res: number) => {
          this.token.price = res;
        });
    },
    checkStarted() {
      this.presaleContract.methods
        .started()
        .call()
        .then((res: boolean) => {
          this.started = res;
        });
    },
    claimToken() {
      this.presaleContract.methods
        .claim()
        .send({ from: this.wallet })
        .then(() => {
          this.checkClaimed();
        });
    },
    checkClaimed() {
      this.presaleContract.methods
        .claimed(this.wallet)
        .call()
        .then((res: number) => {
          this.token.claimed = res;
        });
    },
    checkHardcap() {
      this.presaleContract.methods
        .getHardCap()
        .call()
        .then((res: number) => {
          this.token.hardcap = res;
        });
    },
    setBusdAmount(amount: number) {
      this.token.walletAmount = amount;
    },
    setWallet(address: string) {
      this.wallet = address;
    },
    setBusdContract(contract: any) {
      this.busdContract = contract;
    },
    setPresaleContract(contract: any) {
      this.presaleContract = contract;
    },
    setConnected() {
      this.isConnected = true;
    },
    setWhitelisted() {
      this.isWhitelisted = true;
    },
    setApproved() {
      this.isApproved = true;
    },
    setMaxContribution(max: number) {
      this.token.maxContribution = max;
    },
    setContribution(contribution: number) {
      this.token.contribution = contribution;
    },
    setStarted(bool: boolean) {
      this.started = bool;
    },
    setPrice(price: number) {
      this.token.price = price;
    },
    setClaimed(num: number) {
      this.token.claimed = num;
    },
  },
  getters: {
    getWallet(state): string {
      return state.wallet;
    },
    getBusdAmount(state): number {
      return state.token.walletAmount;
    },
    getBusdContract(state) {
      return state.busdContract;
    },
    getPresaleContract(state) {
      return state.presaleContract;
    },
    getPresaleAddress(state): string {
      return state.addresses.presaleContract;
    },
    getConnected(state): boolean {
      return state.isConnected;
    },
    getWhitelisted(state): boolean {
      return state.isWhitelisted;
    },
    getApproved(state): boolean {
      return state.isApproved;
    },
    getApproveAmount(state): string {
      return state.token.approveAmount;
    },
    getMaxContribution(state): number {
      return state.token.maxContribution;
    },
    getContribution(state): number {
      return state.token.contribution;
    },
    getStarted(state): boolean {
      return state.started;
    },
    getPrice(state): number {
      return state.token.price;
    },
    getClaimed(state): number {
      return state.token.claimed;
    },
    getTotalInvested(state): number {
      return state.token.totalInvested;
    },
    getStartDate(state): Date {
      return state.dates.start;
    },
    getEndDate(state): Date {
      return state.dates.end;
    },
    getHardcap(state): number {
      return state.token.hardcap;
    },
  },
});
