
import { computed, defineComponent } from "vue";
import { useStore } from "@/store/PresaleStore";
import Web3 from "web3";
import { formatAmount } from "@/helper/formatting";
import WalletConnectProvider from "@walletconnect/web3-provider";
import settings from "@/store/settings";

export default defineComponent({
  name: "ConnectionModal",
  props: {
    display: {
      type: Boolean,
      default: false,
    },
  },
  setup() {
    const store = useStore();
    let wallet = computed(() => store.getWallet);

    return {
      store,
      wallet,
      formatAmount,
    };
  },
  methods: {
    close() {
      this.$emit("close");
    },
    connect(): void {
      this.close();
      window.ethereum
        .request({ method: "eth_requestAccounts" })
        .then((res: string[]) => {
          if (res.length >= 1) {
            this.store.setWallet(res[0]);
            this.store.setConnected();
            this.store.loadContracts();
          }
        });
    },
    wcConnect() {
      this.close();
      const wcProvider = new WalletConnectProvider({
        infuraId: "8ce0e87aab13085812fec665882d9028",
        chainId: settings.chainId,
        rpc: {
          97: "https://data-seed-prebsc-1-s1.binance.org:8545",
          56: "https://bsc-dataseed.binance.org/",
        },
      });

      wcProvider.enable().catch((err) => {
        console.log(err);
      });

      window.web3 = new Web3(wcProvider as any);

      window.web3.eth.getAccounts().then((res: string[]) => {
        if (res.length >= 1) {
          this.store.setWallet(res[0]);
          this.store.setConnected();
          this.store.loadContracts();
        }
      });
    },
  },
});
