
import { computed, defineComponent } from "vue";
import { useStore } from "@/store/PresaleStore";
import { formatAmount, timeFormatted } from "@/helper/formatting";

export default defineComponent({
  name: "PresaleInfos",
  setup() {
    const store = useStore();
    const started = computed(() => store.getStarted);
    const presale = computed(() => store.getPresaleContract);
    return {
      store,
      started,
      presale,
      formatAmount,
      timeFormatted,
    };
  },
  watch: {
    started() {
      this.startProgress();
    },
    presale() {
      this.startProgress();
    },
  },
  computed: {
    showStartTimer(): boolean {
      return !this.store.getStarted && this.store.getPrice <= 0;
    },
    showEndTimer(): boolean {
      return this.store.getStarted && this.store.getPrice <= 0;
    },
    progress(): number {
      const value = Math.round(
        (this.store.getTotalInvested /
          10 ** 18 /
          (this.store.getHardcap / 10 ** 18)) *
          100
      );
      return value >= 100 ? 100 : value;
    },
    progressString(): string {
      const totalNum = Math.round(this.store.getTotalInvested / 10 ** 18);
      const limitNum = Math.round(this.store.getHardcap / 10 ** 18);
      const total = formatAmount(this.store.getTotalInvested, 18);
      const limit = formatAmount(this.store.getHardcap, 18);
      return `${totalNum >= limitNum ? limit : total} / ${limit} BUSD`;
    },
  },
  methods: {
    startProgress() {
      if (this.store.getStarted) {
        setInterval(() => {
          this.store.checkTotalInvested();
        }, 2000);
      } else {
        this.store.checkTotalInvested();
      }
    },
  },
});
