export function formatAmount(
  number: number,
  decimals: number,
  fixed = 2
): string {
  number = number / 10 ** decimals;
  let output = "";
  switch (true) {
    case number >= 10 ** 12:
      output = `${(number / 10 ** 12).toFixed(fixed)} T`;
      break;
    case number >= 10 ** 9:
      output = `${(number / 10 ** 9).toFixed(fixed)} B`;
      break;
    case number >= 10 ** 6:
      output = `${(number / 10 ** 6).toFixed(fixed)} M`;
      break;
    case number >= 10 ** 4:
      output = `${(number / 10 ** 3).toFixed(fixed)} K`;
      break;
    default:
      output = `${number.toFixed(fixed)}`;
  }
  return output;
}

export function timeFormatted(h: number, m: number, s: number): string {
  const hour = addZero(h);
  const minute = addZero(m);
  const second = addZero(s);
  return `${hour}:${minute}:${second}`;
}

function addZero(number: number): string | number {
  return number <= 9 ? "0" + number : number;
}
