
import { computed, defineComponent } from "vue";
import { useStore } from "@/store/PresaleStore";
import Web3 from "web3";
import { formatAmount } from "@/helper/formatting";
import busd_abi from "@/assets/abis/busd_abi.json";
import presale_abi from "@/assets/abis/presale_abi.json";
import WalletConnectProvider from "@walletconnect/web3-provider";
import ConnectionModal from "@/components/ConnectionModal.vue";

declare global {
  interface Window {
    ethereum: any;
    web3: any;
  }
}

export default defineComponent({
  name: "App",
  components: { ConnectionModal },
  setup() {
    const store = useStore();
    let wallet = computed(() => store.getWallet);

    return {
      store,
      wallet,

      formatAmount,
    };
  },
  data() {
    return {
      displayConnectModal: false,
      wcProvider: new WalletConnectProvider({
        infuraId: "8ce0e87aab13085812fec665882d9028",
        chainId: 97,
        rpc: {
          97: "https://data-seed-prebsc-1-s1.binance.org:8545",
          56: "https://bsc-dataseed.binance.org/",
        },
      }),
      wcWeb3: {},
    };
  },
  beforeMount(): void {
    this.enableWeb3();
  },
  computed: {
    wallet_obfuscated(): string {
      return `${this.wallet.substring(0, 4)}....${this.wallet.substring(
        this.wallet.length - 4,
        this.wallet.length
      )}`;
    },
  },
  methods: {
    enableWeb3(): void {
      if (window.ethereum) {
        window.web3 = new Web3(window.ethereum);
        this.checkConnection();
      }
    },
    checkConnection(): void {
      window.ethereum
        .request({ method: "eth_accounts" })
        .then((res: string[]) => {
          if (res.length >= 1) {
            this.store.setWallet(res[0]);
            this.store.setConnected();
            this.store.loadContracts();
          }
        });
    },
  },
});
