
import { computed, defineComponent } from "vue";
import { useStore } from "@/store/PresaleStore";
import { formatAmount } from "@/helper/formatting";
import PresaleInfos from "@/components/PresaleInfos.vue";

export default defineComponent({
  name: "PresaleView",
  components: { PresaleInfos },
  setup() {
    const store = useStore();
    const wallet = computed(() => store.getWallet);
    const presale = computed(() => store.getPresaleContract);
    const maxContribution = computed(() => store.getMaxContribution);
    return {
      store,
      wallet,
      presale,
      maxContribution,
      formatAmount,
    };
  },
  data() {
    return {
      contribution: 999999,
      min: 0,
    };
  },
  computed: {
    max(): number {
      const value =
        this.store.getMaxContribution / 10 ** 18 -
        this.store.getContribution / 10 ** 18;
      if (value > this.store.getBusdAmount / 10 ** 18) {
        return this.store.getBusdAmount / 10 ** 18;
      } else {
        return value / 10 ** 18;
      }
    },
    claimString(): string {
      if (this.isClaimed) {
        return "You already claimed";
      } else {
        return `Claim ${(
          (this.store.getContribution - this.store.getClaimed) /
          10 ** 18 /
          (this.store.getPrice / 10 ** 18)
        ).toFixed(2)} PISTON`;
      }
    },
    contributeString(): string {
      return `Contribute`;
      /*return `Contribute ${this.contribution} BUSD`;*/
    },
    isClaimed(): boolean {
      return (
        this.store.getClaimed / 10 ** 18 >=
          this.store.getContribution / 10 ** 18 &&
        this.store.getContribution > 0
      );
    },
    canContribute(): boolean {
      return this.store.getPrice <= 0 && this.store.getStarted;
    },
    canClaim(): boolean {
      return this.store.getPrice > 0 && !this.store.getStarted;
    },
  },
  watch: {
    presale() {
      this.store.loadContractData();
    },
    maxContribution(value: number): void {
      if (value > this.store.getBusdAmount) {
        this.contribution = this.max;
      } else {
        this.contribution = (value - this.store.getContribution) / 10 ** 18;
      }
    },
    max(value) {
      this.contribution = value;
    },
  },
  methods: {
    contribute(): void {
      console.log(`Contribution: $${this.contribution}`);
      this.store.contribute(this.contribution);
    },
    claimToken(): void {
      this.store.claimToken();
    },
    approveContract() {
      this.store.approveContract();
    },
  },
});
